*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  min-width: 360px;
  font-size: 62.5%;
}

@media only screen and (max-width: 1250px) {
  html {
    font-size: 47%;
  }
}

@media only screen and (max-width: 835px) {
  html {
    font-size: 42%;
  }
}

@media only screen and (max-width: 685px) {
  html {
    font-size: 37%;
  }
}

@media only screen and (max-width: 605px) {
  html {
    font-size: 33%;
  }
}

@media only screen and (max-width: 510px) {
  html {
    font-size: 30%;
  }
}

body {
  box-sizing: border-box;
}

/*This is here because react-anchor-link-smooth-scroll and styled-components don't play nice together*/

a[href^="#"] {
  margin: 0 3rem;
  letter-spacing: .2rem;
  font-size: 2rem;
  white-space: nowrap;
}

a[href^="#"]:link, a[href^="#"]:visited {
  color: rgb(242,242,242);
  text-decoration: none;
}

@media only screen and (max-width: 835px) {
  
  a[href^="#"]{
    margin: 0 1.7rem;
    letter-spacing: .15rem;
  }

  a[href^="#"]:first-of-type {
    margin: 0 1.7rem 0 14.7rem;
  }
}

a.header-anchor  {
  display: inline-block;
  position: relative;
  top: 0;
  left: 0;
  letter-spacing: .3rem;
  color: rgb(240,240,240);
  font-family: 'Play', sans-serif;
  font-size: 2.5rem;
  border: .3rem solid rgb(245,245,245);
  border-width: 0 0 .2rem 0;
  padding: .5rem 1rem .7rem 1rem !important;
  background-color: transparent;
  font-weight: 10;
  cursor: pointer;
  margin: 0 !important;
  letter-spacing: .2rem;
  transition: all .4s;
}

@media only screen and (max-width: 1250px) {
  a.header-anchor {
    font-size: 3.5rem;
  }
}


@media only screen and (max-width: 835px) {
  a.header-anchor {
    font-size: 4rem;
  }
}


a.header-anchor:hover {
  transform: scale(1.1);
}